

/*Banner*/
.banner {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: white;
  text-align: center;
  height: 60vh;

}
.banner-title {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: end;
      align-items: flex-end;
  border-bottom: 2px solid white;
}
.banner-title h1 {
  font-size: 70px;
  font-weight: 900;
  color: white;
}
.banner-img{
  padding: 10px 10px 10px 0px;
  width: 100px;
  height: 100px;
  fill: white;
}


/*waypoint */
.scrolling-sub {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #EEEEEE;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
a.active .waypoint {
  color: #fff;
  background-color: #286090;
  border-color: #204d74;
}
.waypoint {
  margin: 10px !important;
}
.fixed {
  position: fixed;
  top: 0px;
  width: 100%;
}
/*custom */
.form-img {
  opacity: .5;
  height: 250px;
  width: 250px;
}



